import { ColumnsConfigs } from '../types';

import createGetLeftToolbarOffset from './createGetLeftToolbarOffset';
import isSelectionInColumns from './isSelectionInColumns';
import { isColumnsChild, isColumnsParent } from './isColumns';

export default function createQueries(configs?: ColumnsConfigs) {
  const getLeftToolbarOffset = createGetLeftToolbarOffset(configs?.isDisabled);
  return {
    getLeftToolbarOffset,
    getLeftToolbarOffsetWithFocus: getLeftToolbarOffset,
    isSelectionInColumns,
    isColumnsChild,
    isColumnsParent,
  };
}
